.container {
    width: 80%;
    margin-left: 10%;
    padding: 10px;
    transition: all ease 0.3s;
    margin: 140px auto;
    min-height: 250px;
    background: #344cb7;
    border-radius: 7px;
    box-shadow: 0px 2px 5px 7px rgba(87, 123, 193, 0.49);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    grid-gap: 10px;
}

.btn {
    width: 150px;
    border-radius: 10px;
    padding: 10px;
    border: none;
    outline: none;
    box-shadow: 0px 2px 5px 7px rgba(87, 123, 193, 0.98);
    cursor: pointer;
    font-size: 25px;
    font-weight: 400;
}

.btn:focus {
    outline: none;
}

.btn-1 {
    background-image: url("../Components/images/crash.png");
    background-position: center;
    object-fit: cover;
}

.btn-2 {
    background-image: url("../Components/images/kick.png");
    background-position: center;
    object-fit: cover;
}

.btn-3 {
    background-image: url("../Components/images/snare.png");
    background-position: center;
    object-fit: cover;
}

.btn-4 {
    background-image: url("../Components/images/tom1.png");
    background-position: center;
    object-fit: cover;
}

.btn-5 {
    background-image: url("../Components/images/tom2.png");
    background-position: center;
    object-fit: cover;
}

.btn-6 {
    background-image: url("../Components/images/tom3.png");
    background-position: center;
    object-fit: cover;
}

@media screen and (max-width: 615px) {
    .container {
        /* width: 90%; */
        /* margin-left: 0px; */
        margin: 20px auto;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    }
    .btn {
        width: 100%;
        height: 150px;
        margin-bottom: 10px;
    }
}